
*{
  margin: 0px;
  padding: 0px;
}
.leaveword{
  border: 1px gray solid;
  height: 818px;
  width: 1024px;
  margin: 0 auto;
}
.word_top{
  height: 668px;
  width: 1024px;
  overflow-y: auto;
}
.write_word{
  height: 150px;
  width: 1024px;
}
.look_text{
  font-size: 25px;
  color: gray;
  height: 128px;
  width: 1024px;
}
.look_people{
  height: 22px;
  width: 1024px;
  border: solid orange 1px;
}
.getup{
    cursor: pointer;
    width: 40px;
    height: 22px;
    background: orange;
    float: right;
}
.require_data{
   height: 50px;
   width: 1022px;
   border-bottom: 1px solid gray;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
}
.require_text{
   font-size: 18px;
   color: gray;
   line-height: 50px;
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
}
.require_name{
   font-size: 20px;
   text-align: center;
   line-height: 50px;
   -webkit-box-flex: 0;
       -ms-flex: 0 0 100px;
           flex: 0 0 100px;
}



